var MyApp = angular.module("MyApp", ["ui.router"]);

MyApp.config(["$stateProvider", "$urlRouterProvider", function($stateProvider, $urlRouterProvider) {
  $urlRouterProvider.otherwise("/intro");

  $stateProvider
    .state("main", {
      url: "/",
      templateUrl: "app/components/main/main.html",
      controller: "MainController",
      controllerAs: "main"
    })
    .state("main.intro", {
      url: "intro",
      templateUrl: "app/components/intro/intro.html",
    })
    .state("main.test", {
      url: "test",
      templateUrl: "app/components/test/test.html",
    })
    .state("stuff", {
      url: "/stuff",
      templateUrl: "app/components/stuff/stuff.html",
      controller: "StuffController",
      controllerAs: "stuffCtrl"
    })
}]);

MyApp.directive("navTop", function() {
  return {
    templateUrl: "app/components/layout-directives/nav-top.html",
  };
});

MyApp.directive("verticalMenu", ["$document", "$window", function($document, $window) {
  return {
    templateUrl: "app/components/layout-directives/vertical-menu.html",
    link: function(scope, element, attrs) {
      var navbar = document.getElementById("navbar");
      var menu = element.find(".fixed-pos")[0];

      scope.menuToggle = [
        true,
        true,
        true,
        true,
      ];

      scope.toggleMenu = function(index) {
        scope.menuToggle[index] = !scope.menuToggle[index];
      }

      var resizeMenu = function() {
        var top = navbar.clientHeight - $document.scrollTop();
        if (top < 0) {
          menu.style.top = 0;
        } else {
          menu.style.top = top + 3 + "px";
        }
      }

      $window.addEventListener("resize", resizeMenu, true);
      $window.addEventListener("scroll", resizeMenu, true);

      scope.$on("$destroy", function() {
        $window.removeEventListener("resize", resizeMenu, true);
        $window.removeEventListener("scroll", resizeMenu, true);
      });

      resizeMenu();
    }
  };
}]);

MyApp.controller("MainController", function() {
  var vm = this;
})

MyApp.controller("StuffController", function() {

})
